import React, { useState, useEffect, useCallback } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import MaterialTable from 'material-table';
import Page from '../../../components/Page';
import reportService from '../../../services/reportService';
import { TextField } from '@material-ui/core';
import { Container, Box, IconButton, SvgIcon, makeStyles } from '@material-ui/core';
import { Edit as EditIcon, ArrowRight as ArrowRightIcon, Trash } from 'react-feather';
import useIsMountedRef from '../../../hooks/useIsMountedRef';
import { useTranslation } from 'react-i18next';
import { DateRange } from 'react-date-range';
import Header from './Header';
import moment from 'moment';
// import Results from './Results';
import 'react-date-range/dist/styles.css'; // main style file
import 'react-date-range/dist/theme/default.css'; // theme css file

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

export default function ChopsAdd() {
  const classes = useStyles();
  const { t } = useTranslation();
  const [needLoad, setNeedLoad] = useState(true);
  const [record, setRecord] = useState();
  const [startTime, setStartTime] = useState(moment().add(-1, 'months'));
  const [endTime, setEndTime] = useState(moment());

  useEffect(() => {
    async function fetch() {
      const data = await reportService.chopsAdd({
        start: startTime.format('YYYY-MM-DD'),
        end: endTime.format('YYYY-MM-DD')
      });
      setRecord(data.data.data);
    }
    if (needLoad) {
      fetch();
      setNeedLoad(false);
    }
  }, [needLoad]);

  const reloadData = () => {
    setNeedLoad(true);
  };

  const handleSelect = (ranges) => {
    const { selection } = ranges;
    const { startDate, endDate } = selection;
    // if (startDate == startTime && endTime == endDate)
    setStartTime(moment(startDate));
    setEndTime(moment(endDate));
  };

  const columns = [
    {
      title: t('Member'),
      field: 'branch',
      render: (rowData) => `${rowData.member.first_name} ${rowData.member.last_name}`
    },
    {
      title: t('Branch'),
      field: 'branch',
      render: (rowData) => `${rowData.branch.name}(${rowData.branch.code})`
    },
    {
      title: t('Type'),
      field: 'type',
      render: (rowData) => t(`ChopsType.${rowData.type}`)
    },
    {
      title: t('Time'),
      field: 'created_at'
    },
    {
      title: t('Earn Chops'),
      field: 'chops'
    }
  ];

  return (
    <Page className={classes.root} title={t('Chops Add Report')}>
      <Container maxWidth={false}>
        <Header
          searchRange={{
            start: startTime,
            end: endTime
          }}
          handleSelect={handleSelect}
          reloadData={reloadData}
        />
        {record && (
          <Box mt={3}>
            <MaterialTable
              title={t('Chops Add Report')}
              options={{
                pageSize: 15,
                pageSizeOptions: [15, 30, 50]
              }}
              columns={columns}
              data={record}
            />
          </Box>
        )}
      </Container>
    </Page>
  );
}
