import axios from '../utils/axios';

class ReportService {
  dashboard = async () => {
    const record = await axios.get('/api/v1/report/dashboard');
    return record;
  };

  prepaidcardTopup = async (query) => {
    const record = await axios.get(
      `/api/v1/report/prepaidcards/topup?start=${query.start}&end=${query.end}`
    );
    return record;
  };

  prepaidcardPayment = async (query) => {
    const record = await axios.get(
      `/api/v1/report/prepaidcards/payment?start=${query.start}&end=${query.end}`
    );
    return record;
  };

  chopsAdd = async (query) => {
    const record = await axios.get(
      `/api/v1/report/chops/add?start=${query.start}&end=${query.end}`
    );
    return record;
  };

  chopsConsume = async (query) => {
    const record = await axios.get(
      `/api/v1/report/chops/consume?start=${query.start}&end=${query.end}`
    );
    return record;
  };

  transactions = async (query) => {
    const record = await axios.get(
      `/api/v1/report/transactions?start=${query.start}&end=${query.end}`
    );
    return record;
  };

  memberRegisterBranchDetail = async (query) => {
    const record = await axios.get(
      `/api/v1/report/memberRegisterBranch/detail?start=${query.start}&end=${query.end}`
    );
    return record;
  };

  memberRegisterBranchStatistics = async (query) => {
    const record = await axios.get(
      `/api/v1/report/memberRegisterBranch/statistics?start=${query.start}&end=${query.end}`
    );
    return record;
  };
}

const reportService = new ReportService();

export default reportService;
